import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { ArrowLeft, Mic, Play, RotateCcw, Trash2, Save, ChevronRight, Star, StarOff, Plus, X } from 'lucide-react';
import { useAudio } from '../hooks/useAudio';

interface AudioItem {
  id: string;
  label: string;
  key: string;
  audioBlob?: Blob;
  isRecorded?: boolean;
}

interface VoiceScheme {
  id: string;
  name: string;
  isDefault: boolean;
  audioItems: AudioItem[];
  description?: string;
  color?: string;
}

const VoiceSelect: React.FC = () => {
  const navigate = useNavigate();
  const { isAudioEnabled } = useAudio();
  const [currentView, setCurrentView] = useState<'list' | 'recording'>('list');
  const [selectedScheme, setSelectedScheme] = useState<VoiceScheme | null>(null);
  const [defaultSchemeId, setDefaultSchemeId] = useState<string>('system');
  const [isRecording, setIsRecording] = useState(false);
  const [recordingItem, setRecordingItem] = useState<string | null>(null);
  const [countdown, setCountdown] = useState<number>(0);
  const [recordedAudios, setRecordedAudios] = useState<Record<string, Blob>>({});
  const [customSchemeName, setCustomSchemeName] = useState('');
  const [showSaveDialog, setShowSaveDialog] = useState(false);
  const [showCreateDialog, setShowCreateDialog] = useState(false);
  const [newSchemeName, setNewSchemeName] = useState('');
  const [customSchemes, setCustomSchemes] = useState<VoiceScheme[]>([]);
  
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const audioChunksRef = useRef<Blob[]>([]);
  const countdownIntervalRef = useRef<NodeJS.Timeout | null>(null);

  // 需要录制的音频项目
  const audioItems: AudioItem[] = [
    { id: '0', label: '数字 0', key: '0' },
    { id: '1', label: '数字 1', key: '1' },
    { id: '2', label: '数字 2', key: '2' },
    { id: '3', label: '数字 3', key: '3' },
    { id: '4', label: '数字 4', key: '4' },
    { id: '5', label: '数字 5', key: '5' },
    { id: '6', label: '数字 6', key: '6' },
    { id: '7', label: '数字 7', key: '7' },
    { id: '8', label: '数字 8', key: '8' },
    { id: '9', label: '数字 9', key: '9' },
    { id: 'plus', label: '加号 +', key: '+' },
    { id: 'minus', label: '减号 -', key: '-' },
    { id: 'multiply', label: '乘号 ×', key: '×' },
    { id: 'divide', label: '除号 ÷', key: '÷' },
    { id: 'equals', label: '等号 =', key: '=' },
    { id: 'decimal', label: '小数点 .', key: '.' },
    { id: 'ge', label: '个', key: '个' },
    { id: 'shi', label: '十', key: '十' },
    { id: 'bai', label: '百', key: '百' },
    { id: 'qian', label: '千', key: '千' },
    { id: 'wan', label: '万', key: '万' },
    { id: 'clear', label: '清空', key: 'clear' },
    { id: 'delete', label: '删除', key: 'delete' },
  ];

  // 计算录音完成度
  const getRecordingProgress = (schemeId: string) => {
    if (schemeId === 'system') return { completed: 23, total: 23 }; // 系统默认已完成
    
    // 对于自定义方案，检查录音状态
    if (schemeId.startsWith('custom_')) {
      const customScheme = customSchemes.find(scheme => scheme.id === schemeId);
      if (customScheme) {
        const completed = customScheme.audioItems.filter(item => item.isRecorded).length;
        return { completed, total: 23 };
      }
    }
    
    // 对于预设方案，检查recordedAudios
    const prefix = `${schemeId}_`;
    const completed = Object.keys(recordedAudios).filter(key => key.startsWith(prefix)).length;
    return { completed, total: 23 };
  };

  // 创建新方案
  const createNewScheme = () => {
    if (!newSchemeName.trim()) {
      alert('请输入方案名称');
      return;
    }
    
    // 检查名称是否重复
    const allSchemes = [...presetSchemes, ...customSchemes];
    if (allSchemes.some(scheme => scheme.name === newSchemeName.trim())) {
      alert('方案名称已存在，请使用其他名称');
      return;
    }
    
    const newSchemeId = `custom_${Date.now()}`;
    const newScheme: VoiceScheme = {
      id: newSchemeId,
      name: newSchemeName.trim(),
      description: '自定义录制的语音方案',
      color: 'bg-indigo-500',
      isDefault: false,
      audioItems: audioItems.map(item => ({
        ...item,
        isRecorded: false
      }))
    };
    
    setCustomSchemes(prev => [...prev, newScheme]);
    setShowCreateDialog(false);
    setNewSchemeName('');
    
    // 自动进入录音界面
    enterRecordingView(newScheme);
  };

  // 预设语音方案列表
  const presetSchemes: VoiceScheme[] = [
    {
      id: 'system',
      name: '系统默认',
      description: '标准普通话发音，适合大多数儿童',
      color: 'bg-blue-500',
      isDefault: defaultSchemeId === 'system',
      audioItems: audioItems
    },
    {
      id: 'dad',
      name: '爸爸的声音',
      description: '让爸爸为你录制专属声音',
      color: 'bg-green-500',
      isDefault: defaultSchemeId === 'dad',
      audioItems: audioItems.map(item => ({
        ...item,
        audioBlob: recordedAudios[`dad_${item.id}`],
        isRecorded: !!recordedAudios[`dad_${item.id}`]
      }))
    },
    {
      id: 'mom',
      name: '妈妈的声音',
      description: '让妈妈为你录制温柔声音',
      color: 'bg-pink-500',
      isDefault: defaultSchemeId === 'mom',
      audioItems: audioItems.map(item => ({
        ...item,
        audioBlob: recordedAudios[`mom_${item.id}`],
        isRecorded: !!recordedAudios[`mom_${item.id}`]
      }))
    },
    {
      id: 'grandpa',
      name: '爷爷的声音',
      description: '让爷爷为你录制慈祥声音',
      color: 'bg-orange-500',
      isDefault: defaultSchemeId === 'grandpa',
      audioItems: audioItems.map(item => ({
        ...item,
        audioBlob: recordedAudios[`grandpa_${item.id}`],
        isRecorded: !!recordedAudios[`grandpa_${item.id}`]
      }))
    },
    {
      id: 'grandma',
      name: '奶奶的声音',
      description: '让奶奶为你录制亲切声音',
      color: 'bg-purple-500',
      isDefault: defaultSchemeId === 'grandma',
      audioItems: audioItems.map(item => ({
        ...item,
        audioBlob: recordedAudios[`grandma_${item.id}`],
        isRecorded: !!recordedAudios[`grandma_${item.id}`]
      }))
    }
  ];

  // 删除自定义方案
  const deleteCustomScheme = (schemeId: string) => {
    if (confirm('确定要删除这个语音方案吗？删除后无法恢复。')) {
      setCustomSchemes(prev => prev.filter(scheme => scheme.id !== schemeId));
      
      // 如果删除的是默认方案，重置为系统默认
      if (defaultSchemeId === schemeId) {
        setDefaultSchemeId('system');
        localStorage.setItem('defaultVoiceScheme', 'system');
      }
      
      // 清理相关的录音数据
      setRecordedAudios(prev => {
        const newRecordings = { ...prev };
        Object.keys(newRecordings).forEach(key => {
          if (key.startsWith(`${schemeId}_`)) {
            delete newRecordings[key];
          }
        });
        return newRecordings;
      });
    }
  };

  // 合并所有语音方案
  const voiceSchemes = [...presetSchemes, ...customSchemes];

  // 开始录音
  const startRecording = async (itemId: string, schemeId?: string) => {
    const audioKey = schemeId ? `${schemeId}_${itemId}` : itemId;
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const mediaRecorder = new MediaRecorder(stream);
      mediaRecorderRef.current = mediaRecorder;
      audioChunksRef.current = [];

      mediaRecorder.ondataavailable = (event) => {
        if (event.data.size > 0) {
          audioChunksRef.current.push(event.data);
        }
      };

      mediaRecorder.onstop = () => {
        const audioBlob = new Blob(audioChunksRef.current, { type: 'audio/wav' });
        setRecordedAudios(prev => ({ ...prev, [audioKey]: audioBlob }));
        
        // 如果是自定义方案，更新其录音状态
        if (schemeId && schemeId.startsWith('custom_')) {
          setCustomSchemes(prev => prev.map(scheme => {
            if (scheme.id === schemeId) {
              return {
                ...scheme,
                audioItems: scheme.audioItems.map(item => 
                  item.id === itemId ? { ...item, isRecorded: true, audioBlob } : item
                )
              };
            }
            return scheme;
          }));
        }
        
        stream.getTracks().forEach(track => track.stop());
      };

      setIsRecording(true);
      setRecordingItem(itemId);
      setCountdown(3);
      mediaRecorder.start();

      // 开始倒计时
      countdownIntervalRef.current = setInterval(() => {
        setCountdown(prev => {
          if (prev <= 1) {
            stopRecording();
            return 0;
          }
          return prev - 1;
        });
      }, 1000);

      // 3秒后自动停止
      setTimeout(() => {
        if (mediaRecorderRef.current && mediaRecorderRef.current.state === 'recording') {
          stopRecording();
        }
      }, 3000);
    } catch (error) {
      console.error('录音失败:', error);
      alert('无法访问麦克风，请检查权限设置');
    }
  };

  // 停止录音
  const stopRecording = () => {
    if (mediaRecorderRef.current && mediaRecorderRef.current.state === 'recording') {
      mediaRecorderRef.current.stop();
    }
    if (countdownIntervalRef.current) {
      clearInterval(countdownIntervalRef.current);
      countdownIntervalRef.current = null;
    }
    setIsRecording(false);
    setRecordingItem(null);
    setCountdown(0);
  };

  // 播放录音或系统默认音频
  const playRecording = (itemId: string, schemeId?: string) => {
    if (schemeId === 'system') {
      // 播放系统默认音频（这里可以播放预设的音频文件）
      const systemAudioUrl = `/audio/system/${itemId}.mp3`; // 假设有预设音频文件
      const audio = new Audio(systemAudioUrl);
      audio.play().catch(() => {
        // 如果没有预设音频文件，播放合成语音
        const utterance = new SpeechSynthesisUtterance(itemId);
        utterance.lang = 'zh-CN';
        utterance.rate = 0.8;
        speechSynthesis.speak(utterance);
      });
    } else {
      // 对于自定义方案，优先从方案数据中获取音频
      if (schemeId && schemeId.startsWith('custom_')) {
        const customScheme = customSchemes.find(scheme => scheme.id === schemeId);
        const audioItem = customScheme?.audioItems.find(item => item.id === itemId);
        if (audioItem?.audioBlob) {
          const audioUrl = URL.createObjectURL(audioItem.audioBlob);
          const audio = new Audio(audioUrl);
          audio.play();
          return;
        }
      }
      
      // 对于预设方案，从recordedAudios中获取
      const audioKey = schemeId ? `${schemeId}_${itemId}` : itemId;
      const audioBlob = recordedAudios[audioKey];
      if (audioBlob) {
        const audioUrl = URL.createObjectURL(audioBlob);
        const audio = new Audio(audioUrl);
        audio.play();
      }
    }
  };

  // 删除录音
  const deleteRecording = (itemId: string, schemeId?: string) => {
    const audioKey = schemeId ? `${schemeId}_${itemId}` : itemId;
    setRecordedAudios(prev => {
      const newRecordings = { ...prev };
      delete newRecordings[audioKey];
      return newRecordings;
    });
    
    // 如果是自定义方案，更新其录音状态
    if (schemeId && schemeId.startsWith('custom_')) {
      setCustomSchemes(prev => prev.map(scheme => {
        if (scheme.id === schemeId) {
          return {
            ...scheme,
            audioItems: scheme.audioItems.map(item => 
              item.id === itemId ? { ...item, isRecorded: false, audioBlob: undefined } : item
            )
          };
        }
        return scheme;
      }));
    }
  };

  // 设置默认方案
  const setAsDefault = (schemeId: string) => {
    setDefaultSchemeId(schemeId);
    // 这里可以保存到本地存储
    localStorage.setItem('defaultVoiceScheme', schemeId);
  };

  // 进入录音界面
  const enterRecordingView = (scheme: VoiceScheme) => {
    setSelectedScheme(scheme);
    setCurrentView('recording');
  };

  // 返回方案列表
  const backToList = () => {
    setCurrentView('list');
    setSelectedScheme(null);
  };

  // 保存自定义语音方案
  const saveCustomScheme = () => {
    if (!customSchemeName.trim()) {
      alert('请输入方案名称');
      return;
    }
    
    if (!selectedScheme || selectedScheme.id === 'system') {
      alert('系统默认方案无需保存');
      return;
    }
    
    // 检查是否有录音内容
    const schemePrefix = `${selectedScheme.id}_`;
    const schemeRecordings = Object.keys(recordedAudios).filter(key => key.startsWith(schemePrefix));
    
    if (schemeRecordings.length === 0) {
      alert('请先录制一些音频内容');
      return;
    }
    
    // 这里可以保存到本地存储或发送到服务器
    const customScheme = {
      id: selectedScheme.id,
      name: customSchemeName,
      isDefault: false,
      audioItems: audioItems.map(item => {
        const audioKey = `${selectedScheme.id}_${item.id}`;
        return {
          ...item,
          audioBlob: recordedAudios[audioKey],
          isRecorded: !!recordedAudios[audioKey]
        };
      })
    };
    
    console.log('保存自定义方案:', customScheme);
    alert(`语音方案"${customSchemeName}"保存成功！已录制 ${schemeRecordings.length}/16 个音频`);
    setShowSaveDialog(false);
    setCustomSchemeName('');
  };

  // 清理资源
  useEffect(() => {
    return () => {
      if (countdownIntervalRef.current) {
        clearInterval(countdownIntervalRef.current);
      }
    };
  }, []);

  // 从本地存储加载默认方案和自定义方案
  useEffect(() => {
    const savedDefault = localStorage.getItem('defaultVoiceScheme');
    if (savedDefault) {
      setDefaultSchemeId(savedDefault);
    }
    
    // 加载自定义方案
    const savedCustomSchemes = localStorage.getItem('customVoiceSchemes');
    if (savedCustomSchemes) {
      try {
        const schemes = JSON.parse(savedCustomSchemes);
        setCustomSchemes(schemes);
      } catch (error) {
        console.error('加载自定义方案失败:', error);
      }
    }
  }, []);
  
  // 保存自定义方案到本地存储
  useEffect(() => {
    if (customSchemes.length > 0) {
      localStorage.setItem('customVoiceSchemes', JSON.stringify(customSchemes));
    }
  }, [customSchemes]);

  // 方案列表视图
  const renderSchemeList = () => (
    <div className="space-y-4">
      <h2 className="text-xl font-bold text-gray-800 mb-6">选择语音方案</h2>
      {voiceSchemes.map(scheme => {
        const progress = getRecordingProgress(scheme.id);
        const isSystemDefault = scheme.id === 'system';
        const isComplete = progress.completed === progress.total;
        
        return (
          <div key={scheme.id} className={`bg-white rounded-xl shadow-lg p-6 hover:shadow-xl transition-shadow ${
            !isSystemDefault && !isComplete ? 'border-2 border-dashed border-gray-300' : ''
          }`}>
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-4">
                <div className={`w-12 h-12 ${scheme.color} rounded-full flex items-center justify-center text-white text-xl font-bold relative`}>
                  {scheme.name.charAt(0)}
                  {isSystemDefault && (
                    <div className="absolute -top-1 -right-1 w-4 h-4 bg-green-500 rounded-full flex items-center justify-center">
                      <span className="text-xs text-white">✓</span>
                    </div>
                  )}
                </div>
                <div className="flex-1">
                  <div className="flex items-center gap-2">
                    <h3 className="text-lg font-semibold text-gray-800">{scheme.name}</h3>
                    {isSystemDefault && (
                      <span className="px-2 py-1 bg-green-100 text-green-700 text-xs rounded-full font-medium">可试听</span>
                    )}
                  </div>
                  <p className="text-sm text-gray-600">{scheme.description}</p>
                  
                  {/* 录音完成度显示 */}
                  {!isSystemDefault && (
                    <div className="flex items-center gap-2 mt-2">
                      <div className="flex-1 bg-gray-200 rounded-full h-2">
                        <div 
                          className={`h-2 rounded-full transition-all duration-300 ${
                            isComplete ? 'bg-green-500' : 'bg-blue-500'
                          }`}
                          style={{ width: `${(progress.completed / progress.total) * 100}%` }}
                        ></div>
                      </div>
                      <span className={`text-xs font-medium ${
                        isComplete ? 'text-green-600' : 'text-blue-600'
                      }`}>
                        {progress.completed}/{progress.total} 已录制
                      </span>
                    </div>
                  )}
                  
                  {scheme.isDefault && (
                    <div className="flex items-center gap-1 mt-1">
                      <Star className="w-4 h-4 text-yellow-500 fill-current" />
                      <span className="text-xs text-yellow-600">默认方案</span>
                    </div>
                  )}
                </div>
              </div>
              <div className="flex items-center gap-2">
                {/* 试听按钮（仅系统默认方案） */}
                {isSystemDefault && (
                  <button
                    onClick={() => playRecording('1', 'system')} // 试听数字1
                    className="p-2 rounded-lg bg-green-100 text-green-600 hover:bg-green-200 transition-colors"
                    title="试听"
                  >
                    <Play className="w-5 h-5" />
                  </button>
                )}
                
                {/* 删除按钮（仅自定义方案） */}
                {scheme.id.startsWith('custom_') && (
                  <button
                    onClick={(e) => {
                      e.stopPropagation();
                      deleteCustomScheme(scheme.id);
                    }}
                    className="p-2 rounded-lg bg-red-100 text-red-600 hover:bg-red-200 transition-colors"
                    title="删除方案"
                  >
                    <X className="w-5 h-5" />
                  </button>
                )}
                
                {/* 设为默认按钮（仅完整方案可设置） */}
                {(isSystemDefault || isComplete) && (
                  <button
                    onClick={() => setAsDefault(scheme.id)}
                    className={`p-2 rounded-lg transition-colors ${
                      scheme.isDefault 
                        ? 'bg-yellow-100 text-yellow-600' 
                        : 'bg-gray-100 text-gray-600 hover:bg-yellow-100 hover:text-yellow-600'
                    }`}
                    title={scheme.isDefault ? '当前默认' : '设为默认'}
                  >
                    {scheme.isDefault ? <Star className="w-5 h-5 fill-current" /> : <StarOff className="w-5 h-5" />}
                  </button>
                )}
                
                <button
                  onClick={() => enterRecordingView(scheme)}
                  className={`flex items-center gap-2 px-4 py-2 rounded-lg transition-colors ${
                    isSystemDefault 
                      ? 'bg-green-500 text-white hover:bg-green-600' 
                      : isComplete
                      ? 'bg-blue-500 text-white hover:bg-blue-600'
                      : 'bg-orange-500 text-white hover:bg-orange-600'
                  }`}
                >
                  <span>
                    {isSystemDefault ? '查看' : isComplete ? '编辑' : '录制'}
                  </span>
                  <ChevronRight className="w-4 h-4" />
                </button>
              </div>
            </div>
          </div>
        );
      })}
      
      {/* 创建新方案按钮 */}
      <div className="bg-white rounded-xl shadow-lg p-6 border-2 border-dashed border-gray-300 hover:border-blue-400 transition-colors">
        <button
          onClick={() => setShowCreateDialog(true)}
          className="w-full flex items-center justify-center gap-3 py-4 text-gray-600 hover:text-blue-600 transition-colors"
        >
          <div className="w-12 h-12 bg-gray-200 rounded-full flex items-center justify-center hover:bg-blue-100 transition-colors">
            <Plus className="w-6 h-6" />
          </div>
          <div className="text-left">
            <h3 className="text-lg font-semibold">创建新方案</h3>
            <p className="text-sm text-gray-500">录制属于你的专属语音</p>
          </div>
        </button>
      </div>
    </div>
  );

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-purple-50 p-4">
      {/* 头部 */}
      <div className="flex items-center justify-between mb-6">
        <button
          onClick={currentView === 'list' ? () => navigate(-1) : backToList}
          className="flex items-center gap-2 px-4 py-2 bg-white rounded-lg shadow-md hover:shadow-lg transition-shadow"
        >
          <ArrowLeft className="w-5 h-5" />
          <span>返回</span>
        </button>
        <h1 className="text-2xl font-bold text-gray-800">
          {currentView === 'list' ? '语音方案' : `录制 - ${selectedScheme?.name}`}
        </h1>
        <div className="w-20"></div>
      </div>

      {/* 主要内容区域 */}
      {currentView === 'list' ? renderSchemeList() : null}

      {/* 条状录音界面 */}
      {currentView === 'recording' && selectedScheme && (
        <div className="bg-white rounded-xl shadow-lg p-6">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-xl font-bold text-gray-800">
              {selectedScheme.id === 'system' ? '系统默认语音' : '录制语音项目'}
            </h2>
            {selectedScheme.id !== 'system' && (
              <button
                onClick={() => setShowSaveDialog(true)}
                className="px-4 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors flex items-center gap-2"
              >
                <Save className="w-4 h-4" />
                保存方案
              </button>
            )}
          </div>
          
          <div className="space-y-3">
            {audioItems.map(item => (
              <div key={item.id} className="bg-gradient-to-r from-blue-50 to-purple-50 rounded-lg p-4 border border-gray-200">
                <div className="flex items-center justify-between">
                  {/* 左侧：标签信息 */}
                  <div className="flex items-center gap-4">
                    <div className="w-12 h-12 bg-blue-500 rounded-full flex items-center justify-center text-white text-xl font-bold">
                      {item.key}
                    </div>
                    <div>
                      <h3 className="text-lg font-semibold text-gray-800">{item.label}</h3>
                      <p className="text-sm text-gray-600">按键: {item.key}</p>
                    </div>
                  </div>
                  
                  {/* 右侧：录音控制 */}
                  <div className="flex items-center gap-3">
                    {/* 录音状态显示 */}
                    {isRecording && recordingItem === item.id ? (
                      <div className="flex items-center gap-3">
                        <div className="relative">
                          <button
                            className="w-12 h-12 bg-red-500 rounded-full flex items-center justify-center text-white shadow-lg"
                            disabled
                          >
                            <Mic className="w-6 h-6" />
                          </button>
                          <div className="absolute -top-1 -right-1 w-3 h-3 bg-red-500 rounded-full animate-pulse"></div>
                        </div>
                        <div className="text-2xl font-bold text-red-500 w-8 text-center">{countdown}</div>
                        <span className="text-sm text-gray-600">录音中...</span>
                      </div>
                    ) : (() => {
                      let hasRecording = false;
                      
                      if (selectedScheme?.id === 'system') {
                        hasRecording = true; // 系统默认总是有录音
                      } else if (selectedScheme?.id?.startsWith('custom_')) {
                        // 自定义方案从方案数据中检查
                        const audioItem = selectedScheme.audioItems.find(audioItem => audioItem.id === item.id);
                        hasRecording = audioItem?.isRecorded || false;
                      } else {
                        // 预设方案从recordedAudios中检查
                        const audioKey = `${selectedScheme?.id}_${item.id}`;
                        hasRecording = !!recordedAudios[audioKey];
                      }
                      
                      return hasRecording ? (
                        // 已录制状态或系统默认
                        <div className="flex items-center gap-2">
                          <span className={`text-sm font-medium ${
                            selectedScheme?.id === 'system' ? 'text-blue-600' : 'text-green-600'
                          }`}>
                            {selectedScheme?.id === 'system' ? '✓ 系统默认' : '✓ 已录制'}
                          </span>
                          <button
                            onClick={() => playRecording(item.id, selectedScheme?.id)}
                            className="w-10 h-10 bg-blue-500 rounded-full flex items-center justify-center text-white hover:bg-blue-600 transition-colors"
                            title={selectedScheme?.id === 'system' ? '试听' : '播放'}
                          >
                            <Play className="w-5 h-5" />
                          </button>
                          {selectedScheme?.id !== 'system' && (
                            <>
                              <button
                                onClick={() => startRecording(item.id, selectedScheme?.id)}
                                className="w-10 h-10 bg-orange-500 rounded-full flex items-center justify-center text-white hover:bg-orange-600 transition-colors"
                                title="重录"
                              >
                                <RotateCcw className="w-5 h-5" />
                              </button>
                              <button
                                onClick={() => deleteRecording(item.id, selectedScheme?.id)}
                                className="w-10 h-10 bg-red-500 rounded-full flex items-center justify-center text-white hover:bg-red-600 transition-colors"
                                title="删除"
                              >
                                <Trash2 className="w-5 h-5" />
                              </button>
                            </>
                          )}
                        </div>
                      ) : (
                        // 未录制状态
                        <div className="flex items-center gap-3">
                          <span className="text-sm text-gray-500">未录制</span>
                          <button
                            onClick={() => startRecording(item.id, selectedScheme?.id)}
                            disabled={isRecording}
                            className="w-12 h-12 bg-blue-500 rounded-full flex items-center justify-center text-white hover:bg-blue-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                            title="开始录音"
                          >
                            <Mic className="w-6 h-6" />
                          </button>
                        </div>
                      );
                    })()}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* 保存对话框 */}
      {showSaveDialog && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl p-6 w-96 max-w-[90vw]">
            <h3 className="text-lg font-semibold text-gray-800 mb-4">保存语音方案</h3>
            <input
              type="text"
              value={customSchemeName}
              onChange={(e) => setCustomSchemeName(e.target.value)}
              placeholder="请输入方案名称"
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent mb-4"
            />
            <div className="flex gap-3">
              <button
                onClick={() => setShowSaveDialog(false)}
                className="flex-1 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
              >
                取消
              </button>
              <button
                onClick={saveCustomScheme}
                className="flex-1 px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
              >
                保存
              </button>
            </div>
          </div>
        </div>
      )}

      {/* 创建新方案对话框 */}
      {showCreateDialog && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl p-6 w-96 max-w-[90vw]">
            <h3 className="text-lg font-semibold text-gray-800 mb-4">创建新语音方案</h3>
            <p className="text-sm text-gray-600 mb-4">为你的专属语音方案起个名字吧！</p>
            <input
              type="text"
              value={newSchemeName}
              onChange={(e) => setNewSchemeName(e.target.value)}
              placeholder="例如：小明的声音"
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent mb-4"
              onKeyPress={(e) => e.key === 'Enter' && createNewScheme()}
            />
            <div className="flex gap-3">
              <button
                onClick={() => {
                  setShowCreateDialog(false);
                  setNewSchemeName('');
                }}
                className="flex-1 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
              >
                取消
              </button>
              <button
                onClick={createNewScheme}
                className="flex-1 px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
              >
                创建并录制
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default VoiceSelect;