import React, { useState, useEffect, useCallback } from 'react';
import { ArrowLeft, RefreshCw, Star, Heart, Grid3X3 } from 'lucide-react';
import { useAudio } from '../../hooks/useAudio';
import StudyDataStorage from '../../lib/studyDataStorage';

interface MultiplicationPracticeProps {
  onBack: () => void;
}

interface Question {
  num1: number;
  num2: number;
  answer: number;
}

interface Stats {
  correct: number;
  total: number;
  stars: number;
}

type Mode = 'table' | 'practice';

const MultiplicationPractice: React.FC<MultiplicationPracticeProps> = ({ onBack }) => {
  const { playNumber, playOperator } = useAudio();
  const [mode, setMode] = useState<Mode>('table');
  const [question, setQuestion] = useState<Question>({ num1: 0, num2: 0, answer: 0 });
  const [userAnswer, setUserAnswer] = useState('');
  const [stats, setStats] = useState<Stats>({ correct: 0, total: 0, stars: 0 });
  const [showResult, setShowResult] = useState(false);
  const [isCorrect, setIsCorrect] = useState(false);
  const [showCelebration, setShowCelebration] = useState(false);
  const [feedback, setFeedback] = useState('');
  const [selectedCell, setSelectedCell] = useState<{row: number, col: number} | null>(null);
  const [sessionId] = useState(() => StudyDataStorage.startSession('practice'));
  const [questionStartTime, setQuestionStartTime] = useState<number>(Date.now());

  // 生成新题目
  const generateQuestion = useCallback(() => {
    const num1 = Math.floor(Math.random() * 9) + 1; // 1-9
    const num2 = Math.floor(Math.random() * 9) + 1; // 1-9
    const answer = num1 * num2;
    
    setQuestion({ num1, num2, answer });
    setUserAnswer('');
    setShowResult(false);
    setShowCelebration(false);
    setFeedback('');
    setQuestionStartTime(Date.now());
    
    // 播放题目语音
    setTimeout(() => {
      playNumber(num1);
      setTimeout(() => {
        playOperator('multiply');
        setTimeout(() => {
          playNumber(num2);
        }, 800);
      }, 800);
    }, 500);
  }, [playNumber, playOperator]);

  // 组件卸载时结束学习会话
  useEffect(() => {
    return () => {
      StudyDataStorage.endSession();
    };
  }, [sessionId]);

  // 页面离开时结束学习会话
  useEffect(() => {
    const handleBeforeUnload = () => {
      StudyDataStorage.endSession();
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, [sessionId]);

  // 检查答案
  const checkAnswer = useCallback(() => {
    const answer = parseInt(userAnswer);
    const correct = answer === question.answer;
    const timeSpent = Date.now() - questionStartTime;
    
    // 记录答题数据
    StudyDataStorage.recordAnswer(
      `${question.num1} × ${question.num2}`,
      answer.toString(),
      question.answer.toString(),
      'multiplication',
      'medium',
      timeSpent
    );
    
    setIsCorrect(correct);
    setShowResult(true);
    
    setStats(prev => ({
      correct: prev.correct + (correct ? 1 : 0),
      total: prev.total + 1,
      stars: prev.stars + (correct ? 1 : 0)
    }));
    
    if (correct) {
      setFeedback('太棒了！答对了！🎉');
      setShowCelebration(true);
      // 播放正确答案，然后生成下一题
      setTimeout(async () => {
        await playNumber(answer);
        // 等待语音播放完成后再生成下一题
        setTimeout(() => {
          generateQuestion();
        }, 2500);
      }, 300);
    } else {
      setFeedback(`不对哦，正确答案是 ${question.answer}，再试试吧！💪`);
      setTimeout(() => {
        playNumber(question.answer);
      }, 500);
    }
  }, [userAnswer, question.answer, playNumber, generateQuestion]);

  // 处理输入
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (/^\d*$/.test(value)) {
      setUserAnswer(value);
    }
  };

  // 处理回车键
  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && userAnswer && !showResult) {
      checkAnswer();
    }
  };

  // 处理乘法表格点击
  const handleCellClick = (row: number, col: number) => {
    setSelectedCell({ row, col });
    const result = row * col;
    
    // 播放语音
    setTimeout(() => {
      playNumber(row);
      setTimeout(() => {
        playOperator('multiply');
        setTimeout(() => {
          playNumber(col);
          setTimeout(() => {
            playOperator('equals');
            setTimeout(() => {
              playNumber(result);
            }, 500);
          }, 500);
        }, 500);
      }, 500);
    }, 100);
  };

  // 庆祝动画组件
  const CelebrationAnimation = () => (
    <div className="fixed inset-0 pointer-events-none z-50">
      {[...Array(20)].map((_, i) => (
        <div
          key={i}
          className="absolute animate-bounce"
          style={{
            left: `${Math.random() * 100}%`,
            top: `${Math.random() * 100}%`,
            animationDelay: `${Math.random() * 2}s`,
            animationDuration: `${1 + Math.random()}s`
          }}
        >
          {['⭐', '🎉', '✨', '🌟', '💫'][Math.floor(Math.random() * 5)]}
        </div>
      ))}
    </div>
  );

  // 乘法表视图
  const renderTable = () => (
    <div className="max-w-4xl mx-auto">
      <div className="text-center mb-6">
        <h2 className="text-3xl font-bold text-purple-600 mb-2">九九乘法表</h2>
        <p className="text-gray-600">点击任意格子听发音，开始练习吧！</p>
      </div>

      <div className="bg-white rounded-3xl shadow-2xl p-6 mb-6">
        <div className="grid grid-cols-10 gap-2">
          {/* 表头 */}
          <div className="bg-purple-100 text-purple-600 font-bold text-center py-3 rounded-lg">×</div>
          {[1, 2, 3, 4, 5, 6, 7, 8, 9].map(col => (
            <div key={col} className="bg-purple-100 text-purple-600 font-bold text-center py-3 rounded-lg">
              {col}
            </div>
          ))}
          
          {/* 表格内容 */}
          {[1, 2, 3, 4, 5, 6, 7, 8, 9].map(row => (
            <React.Fragment key={row}>
              <div className="bg-purple-100 text-purple-600 font-bold text-center py-3 rounded-lg">
                {row}
              </div>
              {[1, 2, 3, 4, 5, 6, 7, 8, 9].map(col => (
                <button
                  key={`${row}-${col}`}
                  onClick={() => handleCellClick(row, col)}
                  className={`py-3 rounded-lg font-bold transition-all duration-200 transform hover:scale-105 ${
                    selectedCell?.row === row && selectedCell?.col === col
                      ? 'bg-yellow-400 text-white shadow-lg scale-105'
                      : 'bg-gray-50 hover:bg-purple-50 text-gray-700'
                  }`}
                >
                  {row * col}
                </button>
              ))}
            </React.Fragment>
          ))}
        </div>
      </div>

      <div className="text-center">
        <button
          onClick={() => {
            setMode('practice');
            generateQuestion();
          }}
          className="bg-gradient-to-r from-purple-500 to-purple-700 hover:from-purple-600 hover:to-purple-800 text-white font-bold text-xl px-8 py-4 rounded-2xl shadow-lg transition-all duration-300 transform hover:scale-105"
        >
          🎯 开始练习
        </button>
      </div>
    </div>
  );

  // 练习视图
  const renderPractice = () => (
    <div className="max-w-2xl mx-auto">
      {/* 统计信息 */}
      <div className="bg-white rounded-2xl shadow-lg p-4 mb-6">
        <div className="flex justify-between items-center">
          <div className="flex items-center gap-4">
            <div className="flex items-center gap-1">
              <Star className="w-5 h-5 text-yellow-500" />
              <span className="font-bold text-yellow-600">{stats.stars}</span>
            </div>
            <div className="text-sm text-gray-600">
              正确率: {stats.total > 0 ? Math.round((stats.correct / stats.total) * 100) : 0}%
            </div>
            <div className="text-sm text-gray-600">
              已完成: {stats.total} 题
            </div>
          </div>
          <div className="flex gap-2">
            <button
              onClick={() => setMode('table')}
              className="flex items-center gap-2 px-3 py-1 bg-purple-100 text-purple-600 rounded-full hover:bg-purple-200 transition-colors"
            >
              <Grid3X3 className="w-4 h-4" />
              <span className="text-sm font-medium">乘法表</span>
            </button>
            <button
              onClick={generateQuestion}
              className="flex items-center gap-2 px-3 py-1 bg-blue-100 text-blue-600 rounded-full hover:bg-blue-200 transition-colors"
            >
              <RefreshCw className="w-4 h-4" />
              <span className="text-sm font-medium">换一题</span>
            </button>
          </div>
        </div>
      </div>

      {/* 题目区域 */}
      <div className="bg-white rounded-3xl shadow-2xl p-8 mb-6">
        <div className="text-center">
          {/* 题目显示 */}
          <div className="mb-8">
            <div className="flex items-center justify-center gap-4 text-4xl font-bold text-gray-800 mb-4">
              <span className="bg-purple-100 text-purple-600 px-4 py-2 rounded-2xl">
                {question.num1}
              </span>
              <span className="text-purple-500">×</span>
              <span className="bg-blue-100 text-blue-600 px-4 py-2 rounded-2xl">
                {question.num2}
              </span>
              <span className="text-gray-400">=</span>
              <span className="text-orange-500">?</span>
            </div>
          </div>

          {/* 输入区域 */}
          <div className="mb-6">
            <input
              type="text"
              value={userAnswer}
              onChange={handleInputChange}
              onKeyPress={handleKeyPress}
              placeholder="输入答案"
              disabled={showResult}
              className="w-32 h-16 text-3xl font-bold text-center border-4 border-purple-300 rounded-2xl focus:border-purple-500 focus:outline-none transition-colors disabled:bg-gray-100"
              autoFocus
            />
          </div>

          {/* 提交按钮 */}
          {!showResult && (
            <button
              onClick={checkAnswer}
              disabled={!userAnswer}
              className="bg-gradient-to-r from-purple-400 to-purple-600 hover:from-purple-500 hover:to-purple-700 disabled:from-gray-300 disabled:to-gray-400 text-white font-bold text-xl px-8 py-4 rounded-2xl shadow-lg transition-all duration-300 transform hover:scale-105 active:scale-95 disabled:transform-none"
            >
              ✅ 提交答案
            </button>
          )}

          {/* 结果反馈 */}
          {showResult && (
            <div className={`p-6 rounded-2xl ${isCorrect ? 'bg-green-100' : 'bg-red-100'}`}>
              <div className={`text-2xl font-bold mb-2 ${isCorrect ? 'text-green-600' : 'text-red-600'}`}>
                {feedback}
              </div>
              {!isCorrect && (
                <button
                  onClick={generateQuestion}
                  className="bg-blue-500 hover:bg-blue-600 text-white font-bold px-6 py-3 rounded-xl transition-colors"
                >
                  🔄 再来一题
                </button>
              )}
            </div>
          )}
        </div>
      </div>

      {/* 鼓励话语 */}
      <div className="text-center">
        <div className="inline-flex items-center gap-2 bg-white rounded-full px-6 py-3 shadow-lg">
          <Heart className="w-5 h-5 text-red-500" />
          <span className="text-gray-600 font-medium">
            {stats.correct === 0 ? '乘法口诀，轻松掌握！' :
             stats.correct < 5 ? '做得不错，继续加油！' :
             stats.correct < 10 ? '太棒了！你很厉害！' :
             '哇！你是乘法小专家！'}
          </span>
        </div>
      </div>
    </div>
  );

  return (
    <div>
      {mode === 'table' ? renderTable() : renderPractice()}
      {showCelebration && <CelebrationAnimation />}
    </div>
  );
};

export default MultiplicationPractice;