import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { ArrowLeft, RefreshCw, Star } from 'lucide-react';
import { useAudio } from '../hooks/useAudio';

interface Question {
  id: number;
  type: 'addition' | 'subtraction';
  object: string;
  emoji: string;
  num1: number;
  num2: number;
  answer: number;
}

interface Stats {
  correct: number;
  total: number;
  stars: number;
}

const VisualMath: React.FC = () => {
  const navigate = useNavigate();
  const { playNumber, playOperator, playCorrect, playWrong } = useAudio();
  
  const [currentQuestion, setCurrentQuestion] = useState<Question | null>(null);
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null);
  const [showResult, setShowResult] = useState(false);
  const [isCorrect, setIsCorrect] = useState(false);
  const [stats, setStats] = useState<Stats>({ correct: 0, total: 0, stars: 0 });
  const [showCelebration, setShowCelebration] = useState(false);

  // 题目模板
  const questionTemplates = [
    { object: '小猫', emoji: '🐱' },
    { object: '苹果', emoji: '🍎' },
    { object: '香蕉', emoji: '🍌' },
    { object: '汽车', emoji: '🚗' },
    { object: '气球', emoji: '🎈' },
    { object: '花朵', emoji: '🌸' },
    { object: '小鸟', emoji: '🐦' },
    { object: '蝴蝶', emoji: '🦋' },
  ];

  // 生成新题目
  const generateQuestion = () => {
    const template = questionTemplates[Math.floor(Math.random() * questionTemplates.length)];
    const type = Math.random() > 0.3 ? 'addition' : 'subtraction'; // 70%加法，30%减法
    
    let num1, num2, answer;
    
    if (type === 'addition') {
      num1 = Math.floor(Math.random() * 5) + 1; // 1-5
      num2 = Math.floor(Math.random() * 5) + 1; // 1-5
      answer = num1 + num2;
    } else {
      num1 = Math.floor(Math.random() * 8) + 3; // 3-10
      num2 = Math.floor(Math.random() * num1) + 1; // 1到num1-1
      answer = num1 - num2;
    }

    const question: Question = {
      id: Date.now(),
      type,
      object: template.object,
      emoji: template.emoji,
      num1,
      num2,
      answer
    };

    setCurrentQuestion(question);
    setSelectedAnswer(null);
    setShowResult(false);
    setIsCorrect(false);
    setShowCelebration(false);
  };

  // 生成选项
  const generateOptions = (correctAnswer: number) => {
    const options = [correctAnswer];
    
    // 生成3个错误答案
    while (options.length < 4) {
      const wrongAnswer = Math.max(0, correctAnswer + Math.floor(Math.random() * 6) - 3);
      if (!options.includes(wrongAnswer) && wrongAnswer >= 0) {
        options.push(wrongAnswer);
      }
    }
    
    // 打乱顺序
    return options.sort(() => Math.random() - 0.5);
  };

  // 处理答案选择
  const handleAnswerSelect = async (answer: number) => {
    if (showResult) return;
    
    setSelectedAnswer(answer);
    setShowResult(true);
    
    const correct = answer === currentQuestion?.answer;
    setIsCorrect(correct);
    
    // 更新统计
    setStats(prev => ({
      correct: prev.correct + (correct ? 1 : 0),
      total: prev.total + 1,
      stars: prev.stars + (correct ? 1 : 0)
    }));
    
    // 播放音效
    if (correct) {
      playCorrect();
      setShowCelebration(true);
      setTimeout(() => setShowCelebration(false), 2000);
    } else {
      playWrong();
    }

    // 2秒后自动生成下一题
    setTimeout(() => {
      generateQuestion();
    }, 2000);
  };

  // 渲染物体
  const renderObjects = (count: number, emoji: string) => {
    return (
      <div className="flex flex-wrap justify-center gap-2 mb-4">
        {Array.from({ length: count }).map((_, index) => (
          <motion.div
            key={index}
            initial={{ scale: 0 }}
            animate={{ scale: 1 }}
            transition={{ delay: index * 0.1 }}
            className="text-3xl"
          >
            {emoji}
          </motion.div>
        ))}
      </div>
    );
  };

  // 初始化
  useEffect(() => {
    generateQuestion();
  }, []);

  if (!currentQuestion) {
    return <div>加载中...</div>;
  }

  const options = generateOptions(currentQuestion.answer);

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-100 via-blue-100 to-purple-100 p-4">
      {/* 顶部导航 */}
      <div className="flex items-center justify-between mb-6">
        <button
          onClick={() => navigate('/')}
          className="flex items-center gap-2 px-4 py-2 bg-white rounded-full shadow-lg hover:shadow-xl transition-all duration-300 transform hover:scale-105"
        >
          <ArrowLeft className="w-5 h-5 text-blue-500" />
          <span className="text-blue-500 font-bold">返回</span>
        </button>
        
        <h1 className="text-2xl font-bold text-blue-600">学数学</h1>
        
        <div className="w-20"></div>
      </div>

      <div className="max-w-2xl mx-auto">
        {/* 统计信息 */}
        <div className="bg-white rounded-2xl shadow-lg p-4 mb-6">
          <div className="flex justify-between items-center">
            <div className="flex items-center gap-4">
              <div className="flex items-center gap-1">
                <Star className="w-5 h-5 text-yellow-500" />
                <span className="font-bold text-yellow-600">{stats.stars}</span>
              </div>
              <div className="text-sm text-gray-600">
                正确率: {stats.total > 0 ? Math.round((stats.correct / stats.total) * 100) : 0}%
              </div>
              <div className="text-sm text-gray-600">
                已完成: {stats.total} 题
              </div>
            </div>
            <button
              onClick={generateQuestion}
              className="flex items-center gap-2 px-3 py-1 bg-blue-100 text-blue-600 rounded-full hover:bg-blue-200 transition-colors"
            >
              <RefreshCw className="w-4 h-4" />
              <span className="text-sm font-medium">换一题</span>
            </button>
          </div>
        </div>

        {/* 题目区域 */}
        <div className="bg-white rounded-3xl shadow-2xl p-8 mb-6">
          <div className="text-center">
            {/* 题目描述 */}
            <div className="text-xl font-bold text-gray-800 mb-6">
              {currentQuestion.num1}只{currentQuestion.object} {currentQuestion.type === 'addition' ? '加上' : '拿走'} {currentQuestion.num2}只{currentQuestion.object}
            </div>

            {/* 第一组物体 */}
            <div className="mb-6">
              <div className="text-lg font-semibold text-gray-600 mb-2">
                {currentQuestion.num1}只{currentQuestion.object}
              </div>
              {renderObjects(currentQuestion.num1, currentQuestion.emoji)}
            </div>

            {/* 运算符 */}
            <div className="text-4xl font-bold text-blue-600 mb-6">
              {currentQuestion.type === 'addition' ? '+' : '-'}
            </div>

            {/* 第二组物体 */}
            <div className="mb-6">
              <div className="text-lg font-semibold text-gray-600 mb-2">
                {currentQuestion.num2}只{currentQuestion.object}
              </div>
              {renderObjects(currentQuestion.num2, currentQuestion.emoji)}
            </div>

            {/* 等号 */}
            <div className="text-4xl font-bold text-blue-600 mb-6">=</div>

            {/* 问号或结果 */}
            <div className="text-6xl font-bold text-orange-500 mb-6">
              {showResult ? currentQuestion.answer : '?'}
            </div>
          </div>
        </div>

        {/* 选项区域 */}
        <div className="grid grid-cols-2 gap-4 mb-6">
          {options.map((option, index) => (
            <motion.button
              key={index}
              onClick={() => handleAnswerSelect(option)}
              disabled={showResult}
              className={`
                relative h-20 rounded-2xl font-bold text-2xl transition-all duration-300 transform
                ${showResult 
                  ? option === currentQuestion.answer
                    ? 'bg-green-500 text-white scale-105'
                    : option === selectedAnswer
                    ? 'bg-red-500 text-white'
                    : 'bg-gray-300 text-gray-500'
                  : 'bg-blue-500 hover:bg-blue-600 text-white hover:scale-105 active:scale-95'
                }
                ${showResult ? 'cursor-not-allowed' : 'cursor-pointer shadow-lg hover:shadow-xl'}
              `}
              whileHover={!showResult ? { scale: 1.05 } : {}}
              whileTap={!showResult ? { scale: 0.95 } : {}}
            >
              {option}
              {showResult && option === currentQuestion.answer && (
                <div className="absolute -top-2 -right-2 text-2xl">✅</div>
              )}
              {showResult && option === selectedAnswer && option !== currentQuestion.answer && (
                <div className="absolute -top-2 -right-2 text-2xl">❌</div>
              )}
            </motion.button>
          ))}
        </div>

        {/* 结果反馈 */}
        {showResult && (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className={`p-6 rounded-2xl text-center ${isCorrect ? 'bg-green-100' : 'bg-red-100'}`}
          >
            <div className={`text-2xl font-bold mb-2 ${isCorrect ? 'text-green-600' : 'text-red-600'}`}>
              {isCorrect ? '太棒了！答对了！' : '再想想哦~'}
            </div>
            {!isCorrect && (
              <div className="text-gray-600">
                正确答案是 {currentQuestion.answer}
              </div>
            )}
          </motion.div>
        )}

        {/* 庆祝动画 */}
        {showCelebration && (
          <div className="fixed inset-0 pointer-events-none z-50">
            {[...Array(20)].map((_, i) => (
              <div
                key={i}
                className="absolute animate-bounce"
                style={{
                  left: `${Math.random() * 100}%`,
                  top: `${Math.random() * 100}%`,
                  animationDelay: `${Math.random() * 2}s`,
                  animationDuration: `${1 + Math.random()}s`
                }}
              >
                {['⭐', '🎉', '✨', '🌟', '💫', '🎊'][Math.floor(Math.random() * 6)]}
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default VisualMath;