// 语音识别结果处理工具

/**
 * 检查文本是否为数学表达式
 * @param text 要检查的文本
 * @returns 是否为数学表达式
 */
export function isMathExpression(text: string): boolean {
  // 移除空格
  const cleanText = text.replace(/\s+/g, '');
  
  // 检查是否包含数字和运算符
  const mathPattern = /^[0-9+\-*/().=\s]+$/;
  const hasNumber = /\d/.test(cleanText);
  const hasOperator = /[+\-*/=]/.test(cleanText);
  
  return mathPattern.test(cleanText) && hasNumber && hasOperator;
}

/**
 * 处理语音识别的数学表达式
 * @param text 语音识别的文本
 * @returns 处理后的数学表达式
 */
export function processVoiceMathExpression(text: string): string {
  if (!text) return '';
  
  let processed = text.toLowerCase().trim();
  
  // 中文数字转换
  const chineseNumbers: { [key: string]: string } = {
    '零': '0', '一': '1', '二': '2', '三': '3', '四': '4',
    '五': '5', '六': '6', '七': '7', '八': '8', '九': '9',
    '十': '10'
  };
  
  // 中文运算符转换
  const chineseOperators: { [key: string]: string } = {
    '加': '+', '加上': '+', '加号': '+',
    '减': '-', '减去': '-', '减号': '-', '负': '-',
    '乘': '*', '乘以': '*', '乘号': '*', '叉': '*',
    '除': '/', '除以': '/', '除号': '/', '分之': '/',
    '等于': '=', '等': '=', '是': '='
  };
  
  // 英文运算符转换
  const englishOperators: { [key: string]: string } = {
    'plus': '+', 'add': '+', 'and': '+',
    'minus': '-', 'subtract': '-', 'negative': '-',
    'times': '*', 'multiply': '*', 'multiplied by': '*',
    'divide': '/', 'divided by': '/', 'over': '/',
    'equals': '=', 'equal': '=', 'is': '='
  };
  
  // 替换中文数字
  Object.entries(chineseNumbers).forEach(([chinese, digit]) => {
    processed = processed.replace(new RegExp(chinese, 'g'), digit);
  });
  
  // 替换中文运算符
  Object.entries(chineseOperators).forEach(([chinese, operator]) => {
    processed = processed.replace(new RegExp(chinese, 'g'), ` ${operator} `);
  });
  
  // 替换英文运算符
  Object.entries(englishOperators).forEach(([english, operator]) => {
    processed = processed.replace(new RegExp(english, 'gi'), ` ${operator} `);
  });
  
  // 清理多余空格
  processed = processed.replace(/\s+/g, ' ').trim();
  
  // 移除空格（最终结果）
  processed = processed.replace(/\s/g, '');
  
  return processed;
}

/**
 * 验证数学表达式是否有效
 * @param expression 数学表达式
 * @returns 是否有效
 */
export function isValidMathExpression(expression: string): boolean {
  try {
    // 基本语法检查
    const cleanExpr = expression.replace(/[^0-9+\-*/.()]/g, '');
    
    // 检查括号是否匹配
    let parenthesesCount = 0;
    for (const char of cleanExpr) {
      if (char === '(') parenthesesCount++;
      if (char === ')') parenthesesCount--;
      if (parenthesesCount < 0) return false;
    }
    
    return parenthesesCount === 0 && cleanExpr.length > 0;
  } catch {
    return false;
  }
}