import React, { useState, useEffect, useCallback } from 'react';
import { ArrowLeft, RefreshCw, Star, Heart, Volume2 } from 'lucide-react';
import { useAudio } from '../../hooks/useAudio';
import StudyDataStorage from '../../lib/studyDataStorage';

interface DivisionPracticeProps {
  onBack: () => void;
}

interface Question {
  num1: number;
  num2: number;
  answer: number;
}

interface Stats {
  correct: number;
  total: number;
  stars: number;
}

const DivisionPractice: React.FC<DivisionPracticeProps> = ({ onBack }) => {
  const { playNumber, playOperator } = useAudio();
  const [question, setQuestion] = useState<Question>({ num1: 0, num2: 0, answer: 0 });
  const [userAnswer, setUserAnswer] = useState('');
  const [stats, setStats] = useState<Stats>({ correct: 0, total: 0, stars: 0 });
  const [showResult, setShowResult] = useState(false);
  const [isCorrect, setIsCorrect] = useState(false);
  const [showCelebration, setShowCelebration] = useState(false);
  const [feedback, setFeedback] = useState('');
  const [sessionId] = useState(() => StudyDataStorage.startSession('practice'));
  const [questionStartTime, setQuestionStartTime] = useState<number>(Date.now());
  const [isPlayingAudio, setIsPlayingAudio] = useState(false);

  // 播放题目语音
  const playQuestionAudio = useCallback(async (num1: number, num2: number) => {
    if (isPlayingAudio) return; // 防止重复播放
    
    setIsPlayingAudio(true);
    try {
      await new Promise(resolve => setTimeout(resolve, 500)); // 初始延迟
      await playNumber(num1);
      await new Promise(resolve => setTimeout(resolve, 400)); // 等待语音播放完成
      await playOperator('÷');
      await new Promise(resolve => setTimeout(resolve, 400)); // 等待语音播放完成
      await playNumber(num2);
    } catch (error) {
      console.error('播放题目语音失败:', error);
    } finally {
      setIsPlayingAudio(false);
    }
  }, [playNumber, playOperator, isPlayingAudio]);

  // 重听题目
  const replayQuestion = useCallback(() => {
    playQuestionAudio(question.num1, question.num2);
  }, [playQuestionAudio, question.num1, question.num2]);

  // 生成新题目
  const generateQuestion = useCallback(async () => {
    // 生成除法题目，确保结果为整数
    const divisors = [2, 3, 4, 5, 6, 7, 8, 9, 10]; // 除数范围
    const num2 = divisors[Math.floor(Math.random() * divisors.length)];
    const answer = Math.floor(Math.random() * 10) + 1; // 商为1-10
    const num1 = num2 * answer; // 被除数 = 除数 × 商
    
    setQuestion({ num1, num2, answer });
    setUserAnswer('');
    setShowResult(false);
    setShowCelebration(false);
    setFeedback('');
    setQuestionStartTime(Date.now());
    setIsPlayingAudio(false);
    
    // 播放题目语音
    playQuestionAudio(num1, num2);
  }, [playQuestionAudio]);

  // 初始化第一题
  useEffect(() => {
    const initFirstQuestion = async () => {
      let num1, num2;
      
      // 生成除法题目，确保结果为整数
      do {
        num2 = Math.floor(Math.random() * 10) + 1; // 1-10的除数
        const quotient = Math.floor(Math.random() * 10) + 1; // 1-10的商
        num1 = num2 * quotient; // 被除数 = 除数 × 商，确保整除
      } while (num1 > 100); // 限制被除数不超过100
      
      const answer = num1 / num2;
      
      setQuestion({ num1, num2, answer });
      setUserAnswer('');
      setShowResult(false);
      setShowCelebration(false);
      setFeedback('');
      setQuestionStartTime(Date.now());
      setIsPlayingAudio(false);
      
      // 播放题目语音
      playQuestionAudio(num1, num2);
    };
    
    initFirstQuestion();
  }, []); // 空依赖数组，只在组件挂载时执行一次

  // 组件卸载时结束学习会话
  useEffect(() => {
    return () => {
      StudyDataStorage.endSession();
    };
  }, [sessionId]);

  // 页面离开时结束学习会话
  useEffect(() => {
    const handleBeforeUnload = () => {
      StudyDataStorage.endSession();
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, [sessionId]);

  // 播放答案反馈语音
  const playAnswerFeedback = useCallback(async (correct: boolean, answer: number, correctAnswer: number) => {
    setIsPlayingAudio(true);
    try {
      if (correct) {
        await new Promise(resolve => setTimeout(resolve, 300)); // 初始延迟
        await playNumber(answer);
        await new Promise(resolve => setTimeout(resolve, 2500)); // 等待语音播放完成
        generateQuestion(); // 生成下一题
      } else {
        await new Promise(resolve => setTimeout(resolve, 500)); // 初始延迟
        await playNumber(correctAnswer);
      }
    } catch (error) {
      console.error('播放答案反馈语音失败:', error);
    } finally {
      setIsPlayingAudio(false);
    }
  }, [playNumber, generateQuestion]);

  // 检查答案
  const checkAnswer = useCallback(() => {
    const answer = parseInt(userAnswer);
    const correct = answer === question.answer;
    const timeSpent = Date.now() - questionStartTime;
    
    // 记录答题数据
    StudyDataStorage.recordAnswer(
      `${question.num1} ÷ ${question.num2}`,
      answer.toString(),
      question.answer.toString(),
      'division',
      'easy',
      timeSpent
    );
    
    setIsCorrect(correct);
    setShowResult(true);
    
    setStats(prev => ({
      correct: prev.correct + (correct ? 1 : 0),
      total: prev.total + 1,
      stars: prev.stars + (correct ? 1 : 0)
    }));
    
    if (correct) {
      setFeedback('太棒了！答对了！🎉');
      setShowCelebration(true);
    } else {
      setFeedback(`不对哦，正确答案是 ${question.answer}，再试试吧！💪`);
    }
    
    // 播放答案反馈语音
    playAnswerFeedback(correct, answer, question.answer);
  }, [userAnswer, question.answer, questionStartTime, playAnswerFeedback]);

  // 处理输入
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (/^\d*$/.test(value)) { // 只允许数字
      setUserAnswer(value);
    }
  };

  // 处理回车键
  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && userAnswer && !showResult) {
      checkAnswer();
    }
  };

  // 庆祝动画组件
  const CelebrationAnimation = () => (
    <div className="fixed inset-0 pointer-events-none z-50">
      {[...Array(20)].map((_, i) => (
        <div
          key={i}
          className="absolute animate-bounce"
          style={{
            left: `${Math.random() * 100}%`,
            top: `${Math.random() * 100}%`,
            animationDelay: `${Math.random() * 2}s`,
            animationDuration: `${1 + Math.random()}s`
          }}
        >
          {['⭐', '🎉', '✨', '🌟', '💫'][Math.floor(Math.random() * 5)]}
        </div>
      ))}
    </div>
  );

  return (
    <div className="max-w-2xl mx-auto">


      {/* 统计信息 */}
      <div className="bg-white rounded-2xl shadow-lg p-4 mb-6">
        <div className="flex justify-between items-center">
          <div className="flex items-center gap-4">
            <div className="flex items-center gap-1">
              <Star className="w-5 h-5 text-yellow-500" />
              <span className="font-bold text-yellow-600">{stats.stars}</span>
            </div>
            <div className="text-sm text-gray-600">
              正确率: {stats.total > 0 ? Math.round((stats.correct / stats.total) * 100) : 0}%
            </div>
            <div className="text-sm text-gray-600">
              已完成: {stats.total} 题
            </div>
          </div>
          <button
            onClick={generateQuestion}
            className="flex items-center gap-2 px-3 py-1 bg-blue-100 text-blue-600 rounded-full hover:bg-blue-200 transition-colors"
          >
            <RefreshCw className="w-4 h-4" />
            <span className="text-sm font-medium">换一题</span>
          </button>
        </div>
      </div>

      {/* 题目区域 */}
      <div className="bg-white rounded-3xl shadow-2xl p-8 mb-6">
        <div className="text-center">
          {/* 题目显示 */}
          <div className="mb-8">
            <div className="flex items-center justify-center gap-4 text-4xl font-bold text-gray-800 mb-4">
              <span className="bg-purple-100 text-purple-600 px-4 py-2 rounded-2xl">
                {question.num1}
              </span>
              <span className="text-purple-500">÷</span>
              <span className="bg-blue-100 text-blue-600 px-4 py-2 rounded-2xl">
                {question.num2}
              </span>
              <span className="text-gray-400">=</span>
              <span className="text-orange-500">?</span>
            </div>
            
            {/* 重听按钮 */}
            <button
              onClick={replayQuestion}
              disabled={isPlayingAudio}
              className="flex items-center gap-2 mx-auto px-4 py-2 bg-purple-100 text-purple-600 rounded-full hover:bg-purple-200 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              <Volume2 className={`w-4 h-4 ${isPlayingAudio ? 'animate-pulse' : ''}`} />
              <span className="text-sm font-medium">
                {isPlayingAudio ? '播放中...' : '重听题目'}
              </span>
            </button>
          </div>

          {/* 输入区域 */}
          <div className="mb-6">
            <input
              type="text"
              value={userAnswer}
              onChange={handleInputChange}
              onKeyPress={handleKeyPress}
              placeholder="输入答案"
              disabled={showResult}
              className="w-32 h-16 text-3xl font-bold text-center border-4 border-orange-300 rounded-2xl focus:border-orange-500 focus:outline-none transition-colors disabled:bg-gray-100"
              autoFocus
            />
          </div>

          {/* 提交按钮 */}
          {!showResult && (
            <button
              onClick={checkAnswer}
              disabled={!userAnswer}
              className="bg-gradient-to-r from-orange-400 to-orange-600 hover:from-orange-500 hover:to-orange-700 disabled:from-gray-300 disabled:to-gray-400 text-white font-bold text-xl px-8 py-4 rounded-2xl shadow-lg transition-all duration-300 transform hover:scale-105 active:scale-95 disabled:transform-none"
            >
              ✅ 提交答案
            </button>
          )}

          {/* 结果反馈 */}
          {showResult && (
            <div className={`p-6 rounded-2xl ${isCorrect ? 'bg-green-100' : 'bg-red-100'}`}>
              <div className={`text-2xl font-bold mb-2 ${isCorrect ? 'text-green-600' : 'text-red-600'}`}>
                {feedback}
              </div>
              {!isCorrect && (
                <button
                  onClick={generateQuestion}
                  className="bg-blue-500 hover:bg-blue-600 text-white font-bold px-6 py-3 rounded-xl transition-colors"
                >
                  🔄 再来一题
                </button>
              )}
            </div>
          )}
        </div>
      </div>

      {/* 鼓励话语 */}
      <div className="text-center">
        <div className="inline-flex items-center gap-2 bg-white rounded-full px-6 py-3 shadow-lg">
          <Heart className="w-5 h-5 text-red-500" />
          <span className="text-gray-600 font-medium">
            {stats.correct === 0 ? '加油！你一定可以的！' :
             stats.correct < 5 ? '做得不错，继续努力！' :
             stats.correct < 10 ? '太棒了！你很聪明！' :
             '哇！你是数学小天才！'}
          </span>
        </div>
      </div>

      {/* 庆祝动画 */}
      {showCelebration && <CelebrationAnimation />}
    </div>
  );
};

export default DivisionPractice;